import {getFocusableNodes} from '@servicenow/behavior-focus';
import {msgTypes, msgStatus, icons} from './constants';
/**
 * AJAX calls require data in x-www-form-urlencoded form. This interceptor converts JSON data into raw format and adds
 * as form data in http request.
 * @param context
 * @example {name:'ssoByUser', userId: 'Abel.Tuter'} -> 'name=ssoByUser&userId=Abel.Tuter'
 */
export const convertDataInterceptor = context => {
	const {payload} = context.coeffects.action;
	const rawData = [];

	Object.keys(payload.data).forEach(parameter => {
		rawData.push(`${parameter}=${encodeURIComponent(payload.data[parameter])}`);
	});
	payload.data = rawData.join('&');
	return {
		...context
	};
};

/**
 * sn-mfa-setup component uses this function to convert alert payload from backend into required format
 * @param messages
 */
export const getAlertMessages = messages => {
	if(!messages)
		return [];

	const newMessages = [];
	messages.forEach(msgConfig => {
		if(msgConfig.message)
			newMessages.push(getAlertMsgConfig(msgConfig));
	});
	return newMessages;
};

/**
 * sn-mfa-setup component uses this function to convert alert payload from backend into required format
 * @param msgConfig
 */

export const getAlertMsgConfig = msgConfig => {
	let status, icon;
	switch (msgConfig.type) {
		case msgTypes.ERROR: status = msgStatus.CRITICAL;
			icon = icons.ALERT_ERR;
			break;
		case msgTypes.SUCCESS: status = msgStatus.POSITIVE;
			icon = icons.SUCCESS;
			break;
		default: status = msgStatus.INFO;
			icon = icons.INFO;
	}
	return {
		content: msgConfig.message,
		action: {
			id: `sn-mfa-alert-msg-${parseInt(Math.random()*100)}`,
			type:'dismiss'
		},
		icon,
		status
	};
};

/**
 * Focus Node
 * @param host
 */
export const handleFocus = host => {
	const {tabbableNodes} = getFocusableNodes(host);
	const {length} = tabbableNodes;
	if(!length)
		return ;
	tabbableNodes[length-1].focus();
};

export const getDataParams = endPoint => ({
	sysparm_processor: 'GlideMultifactorAuthenticator',
	sysparm_scope: 'global',
	sysparm_want_session_messages: 'true',
	sysparm_name: endPoint,
});
