import {createHttpEffect} from '@servicenow/ui-effect-http';
import {actionNames, dataHandlerActions, dataHandlerURLs, msgTypes, endPoints} from '../constants';
import {translatedTexts as translated} from '../i18n';
import {convertDataInterceptor, getAlertMessages, getAlertMsgConfig, getDataParams} from '../utils';

export const preEffect = (url, successActionType, errorActionType) => {
	return {
		...createHttpEffect(url, {
			method: 'POST',
			batch: false,
			dataParam: 'data',
			headers: {
				'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'
			},

			successActionType,
			errorActionType
		}),
		interceptors: [convertDataInterceptor]
	}
};

export const postEffect = (successActionType, errorActionType) => {
	return {
		...createHttpEffect(dataHandlerURLs.POST_MFA, {
			method: 'POST',
			batch: false,
			dataParam: 'data',
			headers: {
				'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'
			},
			successActionType,
			errorActionType,
		}),
		interceptors: [convertDataInterceptor]
	}
};

export const showError = ({state, updateState, action, inValid}) => {
	const messages = inValid ? [getAlertMsgConfig({
		type: msgTypes.ERROR,
		message:translated.responseErr
	})] : [...state.messages,
		getAlertMsgConfig({
			type: msgTypes.ERROR,
			message: action && action.payload ? action.payload.message :translated.responseErr
		})];

	updateState({
		isLoading: false,
		isDisabled: false,
		isRequestInProgress: false,
		messages
	});
};

const parseXMLResponse = action => {
	const parser = new DOMParser();
	const responseXML = parser.parseFromString(action.payload, 'application/xml');
	return responseXML.getElementsByTagName('result');
};

export const fetchPreContentSucceeded = ({action, updateState}) => {
	if(action.payload.code === 200) {
		updateState({
			config: action.payload
		});
	}
	updateState({
		isLoading: false,
		messages: getAlertMessages(action.payload.messages)
	});
};

export const bypassSetupSuccess = ({action, updateState, dispatch}) => {
	if(action.payload.code === 200) {
		updateState({isRequestInProgress: false});
		dispatch(actionNames.SETUP_BYPASSED, {url: action.payload.redirect_to});
		return;
	}
	updateState({messages: getAlertMessages(action.payload.messages), isRequestInProgress: false});
};

export const fetchPostContentSucceeded = ({action, state, updateState, isReset}) => {
	const result = parseXMLResponse(action);
	if(result && result.length > 0 ) {
		const config = {
			qr_code_url: result[0].getAttribute('qrCodeURL'),
			qr_code_text: result[0].getAttribute('qrCodeText'),
			can_disable: result[0].getAttribute('canDisable') === 'true',
			validated: result[0].getAttribute('validated') === 'true',
		};

		config.validated ? updateState({
			config,
			isLoading: false,
			showValidate: false,
			messages: [getAlertMsgConfig({type:msgTypes.SUCCESS, message:translated.alertMfaSuccess})],
			mfaCode: null,
			isRequestInProgress: false,
		}) : updateState({
			config,
			isLoading: false,
			showValidate: true,
			messages: isReset ? [getAlertMsgConfig({type:msgTypes.INFO, message: translated.scanQR})] : [...state.messages],
			mfaCode: null,
			isRequestInProgress: false,
		});
		return;
	}
	showError({state, updateState, inValid:true});
};

export const devicePaired = ({state, action, updateState, dispatch}) => {
	if(state.properties.isUserRequested) {
		const result = parseXMLResponse(action);
		if (result && result.length > 0 && result[0].getAttribute('validated') === 'true') {
			dispatch(dataHandlerActions.MFA_POST_FETCH, {data: getDataParams(endPoints.GET_CONTENT)});
			dispatch(actionNames.SETUP_SUCCEEDED, {url: action.payload.redirect_to});
			return;
		}
		showError({state, updateState, inValid: true});
	} else {
		if(action.payload && action.payload.code === 200) {
			updateState({
				messages: [],
				isRequestInProgress: false
			});
			dispatch(actionNames.SETUP_SUCCEEDED, {url: action.payload.redirect_to});
			return;
		}

		updateState({
			messages: getAlertMessages(action.payload.messages),
			isRequestInProgress: false
		});
	}
	dispatch(actionNames.SETUP_FAILED);
};

export const forgotBrowserSuccess = ({state, updateState}) => {
	if(state.discardForgotBrowser) {
		updateState({
			discardForgotBrowser: false
		});
	} else {
		updateState({
			isRequestInProgress: false,
			messages:[getAlertMsgConfig({type: msgTypes.INFO, message: translated.alertForgotBrowser})]
		});
	}
};

export const resetCodeSuccess = ({action, state, updateState}) => {
	const isReset = true;
	fetchPostContentSucceeded({action, state, updateState, isReset});
};
