import '@servicenow/now-button';
import '@servicenow/now-heading';
import '@servicenow/now-text-link';
import '@servicenow/now-input';
import '@servicenow/now-label-value';
import '@servicenow/now-loader';
import '@servicenow/now-alert';
import '@servicenow/now-toggle';
import '@servicenow/now-rich-text';
import {translatedTexts as translate} from './i18n.js';
import {internalActions, hardCodedLinks} from './constants';
import Fragment from '@servicenow/ui-renderer-snabbdom/lib/fragment';

const renderMfaBtns = state => {
	const {config,showValidate,properties,isDisabled} = state;
	const {isUserRequested} = properties;
	return <Fragment>
		<now-input
			type={'text'}
			label={translate.inputLabel}
			required={true}
			autofocus={true}
			maxlength={6}
			value={state.mfaCode}
			messages={state.txtResponseMessages}
			disabled={!showValidate || isDisabled}
		/>
		<now-button
			label={isUserRequested ? showValidate ? translate.pairDevice : translate.unpairDevice : translate.pairDeviceLogin}
			variant='primary'
			size='md'
			append-to-payload={{target: showValidate ? internalActions.PAIR_DEVICE : internalActions.GEN_CODE}}
			disabled={isDisabled}
		/>
		{!isUserRequested ? <now-button-bare
			label={translate.canBypass(config.bypass_remaining_count)}
			variant='primary'
			size='md'
			append-to-payload={{target: internalActions.BYPASS_SETUP}}
			disabled={!config.can_bypass}
		/>
			: <Fragment>
				{!showValidate ? <now-button-bare 
					label={translate.forgotBrowser} 
					variant="primary" 
					append-to-payload={{target: internalActions.FORGOT_BROWSER}} 
					disabled={isDisabled} 
				/> : null}
				{!showValidate && config.can_disable ? <div className="mfa-disable">
					{translate.disableMfa} <now-toggle 
						checked={isDisabled} 
						manageChecked={true} 
						append-to-payload={{target: internalActions.DISABLE_MFA}} 
						disabled={isDisabled} 
					/>
				</div> : null}
			</Fragment>}
	</Fragment>;
};

const renderMfaBody = state => {
	const {isHorizontalLayout,stepOneContent} = state.properties;

	return <ol className={`mfa-setup-body ${isHorizontalLayout ? '-row' : '-col'}`}>
		<li className={`mfa-content ${isHorizontalLayout ? 'slotted-row' : '-col slotted-col'}`}>
			<div className="mfa-circle">1</div>
			<div className="mfa-text">
				{stepOneContent ? <now-rich-text html={stepOneContent} /> : //picked from NDS Rich text component
					<p>
						{translate.step1Content} <now-text-link 
							label={translate.moreDetail} 
							href={hardCodedLinks.LEARN_MORE} 
							variant='primary' 
							opensWindow={true} />
					</p>}
			</div>
		</li>
		<li className={`mfa-content ${isHorizontalLayout ? '-center' : '-col'}`}>
			<div className="mfa-circle">2</div>
			<div className="mfa-text">
				<p>{translate.step2Content}</p>
				<img src={state.config.qr_code_url}/>
				<now-label-value-inline value={state.config.qr_code_text} label={translate.qrTextLabel}/>
			</div>
		</li>
		<li className={`mfa-content ${isHorizontalLayout ? '' : '-col'}`}>
			<div className="mfa-circle">3</div>
			<div className="mfa-text">
				<p>{translate.step3Content}</p>
				{renderMfaBtns(state)}
			</div>
		</li>
	</ol>;
};

const renderLinks = () => <div className='mfa-setup-links'>
	<now-text-link
		label={translate.learnMore}
		href={hardCodedLinks.LEARN_MORE}
		variant='primary'
		opensWindow={true}
	/>
</div>;

const renderNotification = messages => {
	if(!messages || !messages.length)
		return;

	const alerts = [];
	for(let i=0; i<messages.length; ++i)
		alerts.push(<now-alert {...messages[i]}/>);

	return alerts;
};

export const view = (state, {dispatch}) =>{
	const {heading, isUserRequested} = state.properties;
	if(state.isLoading)
		return renderLoader(true);

	return (
		<div className='mfa-setup-wrapper'>
			{renderLoader(state.isRequestInProgress, 'loader')}
			<div className={state.isRequestInProgress ? 'overlay' : ''}>
				{renderNotification(state.messages)}
				{heading ? <now-heading label={heading} variant='header-secondary'/> : ''}
				{!isUserRequested ? renderLinks() : null}
				{renderMfaBody(state, dispatch)}
			</div>
		</div>
	);
};

const renderLoader = (showLoader, className) => showLoader ? <now-loader label={`${translate.loading}`} size="lg" className={className}/> : null;
