import {createCustomElement} from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import styles from './sn-mfa-setup.scss';
import {actionNames,internalActions} from './constants';
import {actionHandlers} from './actions/coreHandler';
import {view} from './view.js';
import {handleFocus} from './utils';

/**
 * ## MFA-SETUP Component
 * @summary Renders mfa setup component for logged in users
 * @seismicElement sn-mfa-setup
 */
createCustomElement('sn-mfa-setup', {
	renderer: {
		type: snabbdom,
		view
	},
	styles,
	actionHandlers,
	initialState: {
		isLoading: true,
		isRequestInProgress: false,
		mfaCode: null,
		showValidate: true,
		txtResponseMessages: [],
		messages: [],
		config: null,
		isDisabled: false,
		discardForgotBrowser: false
	},
	properties: {
		/**
		 * Heading of the component
		 * @type {string}
		 */
		heading: {
			type: 'string',
			default: '',
			required:false
		},
		/**
		 * Custom content for first step
		 * @type {string}
		 */
		stepOneContent: {
			type: 'string',
			default: '',
			required:false
		},
		/**
		 * If true, stack content will be stacked horizontally. By default, content is stacked vertically.
		 * @type {boolean}
		 */
		isHorizontalLayout: {
			type: 'boolean',
			default: false,
			required: false
		},
		/**
		 * By default, displays content for mfa setup just after login, if true then post login mfa setup is displayed.
		 * @type {boolean}
		 */
		isUserRequested: {
			type: 'boolean',
			default: true,
			required: false
		},
		/**
		 * Will be passed from parent component for eg.- now login will pass QR-code url, text and other required information to render MFA-SETUP
		 * @type Object.<qr_code_url: String, qr_code_text: String, can_bypass: Boolean, bypass_remaining_count: Number>
		 */
		config: {
			default:{
				qr_code_url: '',
				qr_code_text: '',
				can_bypass: false,
				bypass_remaining_count: 0,
			},
			schema: {
				type: 'object',
				properties: {
					qr_code_url: {type: 'string'},
					qr_code_text: {type: 'string'},
					can_bypass: {type: 'boolean'},
					bypass_remaining_count: {type: 'number'},
				}
			}
		}
	},
	eventHandlers: [{
		events: ['keydown'],
		effect({ host, action, dispatch }) {
			const {event} = action.payload;
			if (event.key === 'Enter') {
				event.preventDefault();
				handleFocus(host);
				dispatch(internalActions.ENTER_KEY_PRESSED);
			}
		}  
	}],
	dispatches: {
		/**
		 * Dispatches when the code entered by user is successfully validated by backend.
		 */
		[actionNames.SETUP_SUCCEEDED]:{},
		/**
		 * Dispatches if the user input is incorrect for device pairing.
		 */
		[actionNames.SETUP_FAILED]:{},
		/**
		 * Dispatches if there is option to bypass setup for pre-login mfa setup and user clicks on that link.
		 */
		[actionNames.SETUP_BYPASSED]:{},
		/**
		 * Dispatches when user tries to disable mfa post successfully pairing.
		 */
		[actionNames.SETUP_DISABLED]:{},
		/**
		 * Dispatches when user clicks ‘forget all remembered browsers’ button post successfully pairing.
		 */
		[actionNames.FORGOTTEN_BROWSERS]:{},
		/**
		 * Dispatches when user tries to regenerate the QR code/text again post successfully pairing.
		 */
		[actionNames.CODE_RESET]:{}
	}
});
