import {actionNames, internalActions, dataHandlerActions, codeRegEx, msgStatus, icons, dataHandlerURLs, endPoints, msgTypes} from '../constants';
import {actionTypes} from '@servicenow/ui-core';
import {fetchPreContentSucceeded, bypassSetupSuccess,
	fetchPostContentSucceeded, showError, devicePaired,
	forgotBrowserSuccess, resetCodeSuccess, postEffect, preEffect} from './dataHandler';
import {translatedTexts as translated} from '../i18n';
import {convertDataInterceptor, getDataParams, getAlertMsgConfig} from '../utils';

const pairDevice = (state, dispatch) => {
	state.properties.isUserRequested ? dispatch(internalActions.PAIR_DEVICE_POST, {data:{
		...getDataParams(endPoints.VALIDATE_RESPONSE),
		sysparm_updateUserRecord: 'true',
		sysparm_response: state.mfaCode
	}}) : dispatch(internalActions.PAIR_DEVICE_PRE, {
		data: {
			sysparm_type: 'api.login',
			sysparm_api: 'mfa_setup',
			sysparm_process: 'validate',
			txtResponse: state.mfaCode
		}
	});
};

const validateCode = (state, updateState)=> {
	if(!codeRegEx.test(state.mfaCode)) {
		updateState({
			txtResponseMessages: [{
				status: msgStatus.CRITICAL,
				icon: icons.ERROR,
				content: translated.codeInputLabelErrMsg
			}]
		});
		return false;
	}
	return true;
};

export const actionHandlers = {

	[actionTypes.COMPONENT_CONNECTED]({state, dispatch, updateState}) {
		const {isUserRequested, config}=state.properties;
		updateState({
			config
		});

		if(isUserRequested)
			dispatch(dataHandlerActions.MFA_POST_FETCH, {data: getDataParams(endPoints.GET_CONTENT)});
		else if(!config || !config.qr_code_url)
			dispatch(dataHandlerActions.MFA_PRE_FETCH, {data: {
				'sysparm_type': 'api.login',
				'sysparm_api': 'mfa_setup',
				'sysparm_process': 'setup_data'
			}});
		else {
			updateState({
				isLoading: false
			});
		}
	},

	[dataHandlerActions.MFA_PRE_FETCH]: preEffect(dataHandlerURLs.DEFAULT_ROUTE,
		dataHandlerActions.PRE_FETCH_SUCCEDDED,
		dataHandlerActions.REQUEST_FAILED
	),

	[dataHandlerActions.PRE_FETCH_SUCCEDDED]: fetchPreContentSucceeded,

	[dataHandlerActions.MFA_POST_FETCH]: {
		...postEffect(dataHandlerActions.POST_FETCH_SUCCEDDED, dataHandlerActions.REQUEST_FAILED),
		interceptors: [convertDataInterceptor]
	},

	[dataHandlerActions.POST_FETCH_SUCCEDDED]: fetchPostContentSucceeded,

	['NOW_INPUT#INPUT']: function({state, updateState}) {
		if(state.txtResponseMessages.length > 0){
			updateState({
				...state,
				txtResponseMessages: []
			});
		}
	},

	['NOW_INPUT#INVALID_SET']: function({action, state, updateState}) {
		updateState({
			...state,
			mfaCode: action.payload.fieldValue
		});
	},

	['NOW_BUTTON_BARE#CLICKED']: ({action, dispatch, updateState}) => {
		const {target} = action.payload;
		let isRequestInProgress = false;
		switch (target) {
			case internalActions.BYPASS_SETUP:
				dispatch(internalActions.BYPASS_SETUP, {data: {
					'sysparm_type': 'api.login',
					'sysparm_api': 'mfa_setup',
					'sysparm_process': 'bypass'
				}});
				isRequestInProgress = true;
				break;
			case internalActions.FORGOT_BROWSER:
				if(confirm(translated.confForgotBrowser)) {
					dispatch(internalActions.FORGOT_BROWSER, {data: getDataParams(endPoints.FORGOT_BROWSER)});
					isRequestInProgress = true;
				}
				break;
		}

		if(isRequestInProgress)
			updateState({isRequestInProgress});
	},

	['NOW_TOGGLE#CHECKED_SET']: ({updateState, dispatch, action}) => {
		if(action.payload.value && confirm(translated.confDisableMfa)) {
			dispatch(internalActions.FORGOT_BROWSER, {data: getDataParams(endPoints.FORGOT_BROWSER)});
			dispatch(internalActions.DISABLE_MFA, {data: getDataParams(endPoints.DISABLE_MFA)});
			updateState({
				isRequestInProgress: true,
				discardForgotBrowser: true // Discard forgot browser success action
			});
		}
	},

	['NOW_ALERT#ACTION_CLICKED']: ({state, updateState, action}) => {
		const messages = [];
		state.messages.forEach(msgConfig => {
			if(msgConfig.action.id !== action.payload.action.id)
				messages.push(msgConfig);
		});
		updateState({
			messages
		});
	},

	['NOW_BUTTON#CLICKED']: ({state, action, dispatch, updateState}) => {
		const {target} = action.payload;
		switch (target) {
			case internalActions.GEN_CODE:
				if(confirm(translated.confResetCode)) {
					dispatch(internalActions.FORGOT_BROWSER, {data: getDataParams(endPoints.FORGOT_BROWSER)});
					dispatch(internalActions.GEN_CODE, {data: getDataParams(endPoints.RESET_CODE)});
					updateState({
						isRequestInProgress: true,
						discardForgotBrowser: true // Discard forgot browser success action
					});
				}
				break;
			case internalActions.PAIR_DEVICE:
				if(validateCode(state, updateState)) {
					pairDevice(state, dispatch);
					updateState({isRequestInProgress: true});
				}
				break;
		}
	},

	[internalActions.PAIR_DEVICE_POST]: {
		...postEffect(dataHandlerActions.CODE_VALIDATED, dataHandlerActions.REQUEST_FAILED),
		interceptors: [convertDataInterceptor]
	},

	[internalActions.PAIR_DEVICE_PRE]: preEffect(dataHandlerURLs.DEFAULT_ROUTE,
		dataHandlerActions.CODE_VALIDATED,
		dataHandlerActions.REQUEST_FAILED
	),

	[dataHandlerActions.CODE_VALIDATED]: devicePaired,

	[internalActions.ENTER_KEY_PRESSED]({state, updateState, dispatch}){
		if(state.showValidate && validateCode(state, updateState))
			pairDevice(state, dispatch);
	},

	[internalActions.BYPASS_SETUP]: preEffect(dataHandlerURLs.DEFAULT_ROUTE,
		dataHandlerActions.BYPASS_SUCCEDDED,
		dataHandlerActions.REQUEST_FAILED
	),

	[dataHandlerActions.BYPASS_SUCCEDDED]: bypassSetupSuccess,

	[internalActions.GEN_CODE]: {
		...postEffect(actionNames.CODE_RESET, dataHandlerActions.REQUEST_FAILED),
		interceptors: [convertDataInterceptor]
	},

	[actionNames.CODE_RESET]: resetCodeSuccess,

	[internalActions.FORGOT_BROWSER]: {
		...postEffect(actionNames.FORGOTTEN_BROWSERS, dataHandlerActions.REQUEST_FAILED),
		interceptors: [convertDataInterceptor]
	},

	[actionNames.FORGOTTEN_BROWSERS]: forgotBrowserSuccess,

	[actionNames.SETUP_DISABLED]({updateState,dispatch}) {
		updateState({
			messages:[getAlertMsgConfig({type: msgTypes.SUCCESS, message: translated.alertDisableMFA})]
		});
		dispatch(dataHandlerActions.MFA_POST_FETCH, {data: getDataParams(endPoints.GET_CONTENT)});
	},

	[internalActions.DISABLE_MFA]: {
		...postEffect(actionNames.SETUP_DISABLED, dataHandlerActions.REQUEST_FAILED),
		interceptors: [convertDataInterceptor]
	},

	[dataHandlerActions.REQUEST_FAILED]: showError,

};
